<template>
  <div class="article-page">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>面经后台</el-breadcrumb-item>
      <el-breadcrumb-item>面经管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="never" border="false">
      <template #header>
        <div class="header">
          <span>共{{ total }}条记录</span>
          <el-button
            icon="el-icon-plus"
            size="small"
            type="primary"
            round
            @click="open('add')"
          >
            添加面经
          </el-button>
        </div>
      </template>
      <!-- 表格 -->
      <!-- data属性 是表格的数据 -->
      <!-- el-table-column表示表格的列 -->
      <!-- el-table-column label 属性 表格的标题文本 -->
      <!-- el-table-column prop 数据项 -->
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="stem" label="标题" width="400">
        </el-table-column>
        <el-table-column prop="creator" label="作者" />
        <el-table-column prop="likeCount" label="点赞" />
        <el-table-column prop="views" label="浏览数" />
        <el-table-column prop="createdAt" label="更新时间" width="200" />
        <el-table-column label="操作" width="120">
          <!-- 默认插槽   #default  -->
          <!-- 当这个组件被使用时，如果没有提供其他插槽内容，
              那么这个 "default" 插槽的内容将被渲染到组件的相应位置。 -->
          <template #default="{ row }">
            <div class="actions">
              <!-- 预览 -->
              <i class="el-icon-view" @click="open('preview', row)"></i>
              <!-- 编辑 -->
              <i class="el-icon-edit-outline" @click="open('edit', row)"></i>
              <i class="el-icon-delete" @click="Del(row.id)"></i>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <!-- 分页 -->
      <el-pagination
        @size-change="handleSizeChange"
        background
        @current-change="handleCurrentChange"
        :current-page="current"
        layout="total,sizes,prev, pager, next, jumper"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        :total="total"
      >
      </el-pagination>
      <!-- 抽屉 -->
    </el-card>
    <!-- rtl从右往左，ltr从左往右，ttb从上往下，btt从下往上 -->
    <el-drawer
      :title="menu[type]"
      :visible.sync="isShow"
      direction="rtl"
      size="60%"
      @closed="resetForm"
    >
      <!-- <span>我来啦!</span> -->
      <div v-if="type === 'preview'" class="article-preview">
        <h5>{{ form.stem }}</h5>
        <div v-html="form.content"></div>
      </div>
      <el-form
        v-else
        :model="form"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="标题" prop="stem">
          <el-input v-model="form.stem"></el-input>
        </el-form-item>

        <el-form-item label="内容" prop="content">
          <!-- 当输入框失去焦点时，触发表单验证规则 -->
          <quill-editor
            v-model="form.content"
            @blur="$refs.ruleForm.validateField('content')"
          ></quill-editor>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSumbit">确认</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-drawer>
  </div>
</template>

<script>
import { getArticleList, addArticle, updateArticleDetail, delArticle } from '@/api/article'
// require styles
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'
export default {
  name: 'article-page',
  components: {
    quillEditor
  },
  data () {
    return {
      current: 1, // 当前页码
      pageSize: 10, // 每页显示多少条数据
      total: 0, // 记录总条数
      list: [], // 保持数据
      // pageTotal: 0, // 40页
      isShow: false,
      menu: {
        add: '添加面经',
        edit: '修改面经',
        preview: '预览面经'
      },
      type: '', // 保持类别
      // 表单数据
      form: {
        stem: '',
        content: ''
      },
      rules: {
        stem: [{ required: true, message: '请输入面经标题', trigger: 'blur' }],
        content: [
          { required: true, message: '请输入面经标题', trigger: 'blur' }
        ]
      }
    }
  },

  created () {
    this.initData()
  },
  methods: {

    // 获取到的数据
    async initData () {
      const { data } = await getArticleList({
        current: this.current,
        pageSize: this.pageSize
      })
      this.list = data.rows // list
      this.total = data.total // 共400条记录
      // this.pageTotal = data.pageTotal // 40页
      // console.log(data)
    },
    // 显示抽屉
    open (val, data) {
      // console.log(this.type)
      this.type = val
      // 显示抽屉
      this.isShow = true
      if (val === 'edit') {
        // this.form = data
        this.form = { ...data }
      }
      if (val === 'preview') {
        // this.form = data
        this.form = { ...data }
      }
    },
    // 面经预览
    resetForm () {
      if (this.type !== 'preview') {
        this.$refs.ruleForm.resetFields()
      }
      // this.$refs.ruleForm && this.$refs.ruleForm.resetFields()
      this.form = {
        stem: '',
        content: ''
      }
    },
    // 添加面经---------修改面经
    async onSumbit () {
      if (this.type === 'add') {
        // console.log('ok')

        try {
          // 等待异步操作 addArticle(this.form) 完成
          await addArticle(this.form)
          // 显示成功消息
          this.$message.success('添加成功')
          // 重新加载数据
          this.initData()
          // 关闭弹窗
          this.isShow = false
          // 重置表单
          this.resetForm()
        } catch (error) {
          // 如果请求出错，显示错误消息
          if (error.response) this.$message.error(error.response.data.message)
        }
      } else if (this.type === 'edit') {
        // console.log('ok')
        try {
          // 等待异步操作
          await updateArticleDetail({
            id: this.form.id,
            stem: this.form.stem,
            content: this.form.content
          })
          // 显示成功消息
          this.$message.success('修改成功')
          // 重新加载数据
          this.initData()
          // 关闭弹窗
          this.isShow = false
          // 重置表单
          this.resetForm()
        } catch (error) {
          // 如果请求出错，显示错误消息
          if (error.response) this.$message.error(error.response.data.message)
        }
      }
    },
    // 分页功能-----分页
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pageSize = val
      this.initData()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.current = val
      // 重新获取数据
      this.initData()
    },
    // 删除
    Del (id) {
      this.$confirm('确定删除该文件吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        // 点击确定按钮
        // 发送请求 删除
        try {
          await delArticle(id)
          this.$message.success('删除成功')
          this.initData()
        } catch (error) {
          if (error.response) this.$message.error(error.response.data.message)
        }
      }).catch(() => {
        this.$message({
          // 点击取消
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-card {
  margin-top: 25px;
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 16px;
  }
  .actions {
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    color: #666;
    > i:hover {
      color: rgba(114, 124, 245, 1);
      cursor: pointer;
    }
  }
}
.el-pagination {
  margin-top: 20px;
  text-align: center;
}
.el-breadcrumb {
  margin-top: 10px;
}
.el-form {
  padding-right: 40px;
}
.quill-editor {
  ::v-deep .ql-editor {
    height: 300px;
  }
}
.el-rate {
  padding: 10px 0;
}
.article-preview {
  padding: 0 40px 40px 40px;
  > h5 {
    font-size: 20px;
    color: #666;
    border-bottom: 1px dashed #ccc;
    padding-bottom: 30px;
    margin: 0 0 20px 0;
  }
}
</style>
